<template>
  <singLogIn>
    <form @submit.prevent="validateBeforeSubmit">
      <label for="login">
         </label>
      <input id="login" type="text" name="login" v-model="login" placeholder=" "><br>
      <label for="password"> </label>
      <input v-validate="'required|confirmed:repeatPassword'" data-vv-delay="500" :class="{'input': true, 'is-danger': errors.has('password') }" id="password" type="password" name="password" v-model="password" placeholder=" "><br>
      <label for="repeatPassword"> </label>
      <input v-validate="'required'" data-vv-delay="500" :class="{'input': true, 'is-danger': errors.has('repeatPassword') }" id="repeatPassword" name="repeatPassword" type="password" v-model="repeatPassword" placeholder=" "><br>
      <label for="phone"> </label>
      <masked-input v-validate="'required'" data-vv-delay="500" :class="{'input': true, 'is-danger': errors.has('phone') }" id="phone" type="text" name="phone" v-model="phone" :mask="['+', '3', '7', '5', ' ', '(', /[2-4]/, /[3,4,5,9]/, ')', ' ', /[1-9]/, /\d/, /\d/, '-', /\d/, /\d/, '-', /\d/, /\d/]" :guide="true" placeholderChar="X" placeholder="+375 (XX) XXX-XX-XX"></masked-input> <br>
      <label for="email">  e-mail</label>
      <input v-validate="'required|email'" data-vv-delay="500" :class="{'input': true, 'is-danger': errors.has('email') }" id="email" type="text" name="email" v-model="email" placeholder="  e-mail"><br>

      <button class="btnRegistration"></button>
      <button @click.prevent="moveToAuthorization()" class="btnBack">   </button>
      <span class="errorInfo">{{errors.first('password') || errors.first('repeatPassword') || errors.first('phone') || errors.first('email') || errorInfo}} &nbsp;</span>
    </form>
  </singLogIn>
</template>

<script>

export default {
  name: 'registration',
  data() {
    return {
      login: '',
      password: '',
      repeatPassword: '',
      phone: '',
      email: '',
      errorInfo: ''
    }
  },
  methods: {
    validateBeforeSubmit() {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.sendingData();
          return;
        }
      });
    },
    sendingData: function() {
      fetch(' https://test-task-api.insirion.ru/user/registration', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          email: this.email,
          password: this.password,
          phone: this.phone
        })
      }).then((response) => {
        if (response.ok) {
          this.$router.replace('/authorization');
        }
        throw new Error(` -- ${response.status} ${response.statusText}`)
      }).catch(error => {
        this.errorInfo = `${error.message}`

      })
    },
    moveToAuthorization: function() {
      this.$router.replace('/authorization');
    }
  }

}
</script>

<style src="../style/singLogIn.css"></style>



